import React, { useContext } from "react"
import AppContext from '../contextAPI/AppContext'
import { navigate } from "gatsby"
import SVLogo from '../images/SVLogo.jpeg'
import OLLogo from '../images/SVOriginalLanguagesLogo.png'
import SEO from "../components/seo"
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';

import { makeStyles } from '@material-ui/core/styles';
  
const useStyles = makeStyles(theme => ({
  typography: {
    padding: theme.spacing(2),
    fontSize: 'small',
    color: 'white'
  },
  icons: {
    display: 'flex',
    height: 'fit-content',
    width: '100%',
    margin: '0 auto',
    flexDirection: 'column',
    flexWrap: 'nowrap',
    marginBottom: '20px',
  },
  SVLogo: {
    position: 'relative',
    width: '200px',
    height: '200px',
    flex: '1',
    margin: '0 auto'
  },
  slogan: {
    fontStyle: 'italic',
    textAlign: 'center'
  },
  selectLanguage: {
    display: 'flex',
    justifyContent: 'space-around',
    width: '100%',
    flexWrap: 'wrap'
  },
  languageButton: {
    margin: '2px',
    width: 'fit-content'
  }
}))

const IndexPage = () => {
  const classes = useStyles()
  const receivedAppContext = useContext(AppContext)
  const selectLanguage = (language, version, segment) => {
    receivedAppContext.updateLanguage(language)
    receivedAppContext.updateVersion(version)
    navigate(`/${language}/${version}/${segment}`)
  }

return (
  <div>
    <SEO title="Home" />
    <div className={classes.icons}>
      <img src={SVLogo} className={classes.SVLogo} alt="SourceView Bible Logo" />
      <Typography variant="h5" className={classes.slogan} >A Fresh Perspective on the Ancient Scriptures</Typography>
    </div>
    <Divider />
    <div className={classes.icons}>
      <img src={OLLogo} className={classes.SVLogo} alt="SV Languages Logo"/>
      <div className={classes.selectLanguage}>
        <Button size="small" color="secondary" variant="contained" className={classes.languageButton} onClick={() => selectLanguage('ENG', 'NLT', 'I042')} >English</Button>
        <Button size="small" color="secondary" variant="contained" className={classes.languageButton} onClick={() => selectLanguage('HBR', 'WLC', 'I032')} >OT Hebrew</Button>
        <Button size="small" color="secondary" variant="contained" className={classes.languageButton} onClick={() => selectLanguage('GRK', 'GNA', 'I042')} >NT Greek</Button>
      </div>
    </div>
    {/* <p onClick={() => {
      receivedAppContext.updateLanguage("GRK")
      receivedAppContext.updateVersion("GNA")
      navigate("/GRK/GNA/I042")}}
    >GREEK</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("HBR")
      receivedAppContext.updateVersion("WLC")
      navigate("/HBR/WLC/I032")}}
    >HEBREW</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("ENG")
      receivedAppContext.updateVersion("NLT")
      navigate("/ENG/NLT/I040")}}
    >ENGLISH: NLT</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("ENG")
      receivedAppContext.updateVersion("WEB")
      navigate("/ENG/WEB/I042")}}
    >ENGLISH: WEB</p> */}
  </div>
)
}

export default IndexPage
